import Vue from 'vue';
import Vuex from 'vuex';
import router from '../router';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    dictionary: {
      count: '文章數',
      coin: '巴幣',
      gp: 'GP',
      view: '觀看數',
    },
    ownerId: '',
    allData: [],
    isLoading: false,
    dashboardConfig: {
      barchartFilterConfig: {
        dataType: 'view',
        dataSortBy: 'date',
        dataLength: -1,
        dataRange: false,
        dataRangeMin: 0,
        dataRangeMax: 99999999,
      },
      scatterchartFilterConfig: {
        dataType: 'date',
        dataLength: -1,
        viewRange: false,
        viewRangeMin: 0,
        viewRangeMax: 99999999,
        gpRange: false,
        gpRangeMin: 0,
        gpRangeMax: 99999999,
      },
    },
  },
  mutations: {
    setIsLoading(state, payload) {
      state.isLoading = payload;
    },
    setOwnerId(state, payload) {
      state.ownerId = payload;
    },
    setData(state, payload) {
      state.allData = payload;
    },
    clearData(state) {
      state.allData = [];
      sessionStorage.clear('cacheData');
    },
    createNotify(state, payload) {
      const options = {
        duration: 3000,
        offset: 80,
      };

      Object.assign(options, payload);
      this._vm.$notify(options);// eslint-disable-line
    },
  },
  actions: {
    fetchData({ commit }, payload) {
      commit('setIsLoading', true);
      fetch(`https://bahamut-home-article-cralwer.herokuapp.com?owner=${payload}`)
        .then((res) => res.json())
        .then(({ success, data }) => {
          commit('setIsLoading', false);
          if (success) {
            sessionStorage.setItem('cacheData', JSON.stringify(data));
            commit('setData', data);
            commit('createNotify', {
              title: '資料載入完成',
              type: 'success',
            });
            router.push('/dashboard');
          } else {
            commit('createNotify', {
              title: data.message,
              type: 'error',
              duration: 4000,
            });
          }
        })
        .catch((err) => {
          console.log(err);
          commit('createNotify', {
            title: '發生錯誤，請稍後重試或至提問箱回報',
            type: 'error',
            duration: 5000,
          });
        });
    },
  },
  modules: {
  },
});
